@import "../scss/global.scss";


body {
    width: 100vw;
    height: auto;
    //opacity: 0.5;
    background-color: $qanat_sand;
    
}

Header {
    height: 10%;
    //border-bottom: solid 0px white;
}

#teamFirstOption {
    display: flex;
    flex-flow: row nowrap;
    flex: 1;
    gap: 20px;
    background-color: $qanat_sand;
    padding: 20px;
    max-height: 40%;
    width: 100%;
    border: 4px solid $qanat_blue;
    margin: 0px auto;
    div {
        h2 {
            display: inline;
            margin: 20px auto;
            color: $qanat_blue;
            font-size: 40px;
            font-family: $primary-font-bold;
            margin: 0 auto;
            text-align: left;
        }
        p{
            font-family: $primary-font;
            font-size: $xlarge;
            color: $qanat_blue;
            font-weight: normal;
            letter-spacing: 1px;
            margin-top: 50px;
            text-align: left;
            display: block;
            width:fit-content;
        }
    }
    img {
        max-height: 40%;
    }
}

Footer {
    height: 10%;
}


